body, html, #root, .App {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'JetBrains Mono', monospace;
  background: #3a22c4;
  overflow: hidden;
}

.App {
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  background: #3a22c4;
  position: relative;
}

.sidebar-overlay {
  position: absolute; 
  top: 0;
  left: 0;
  height: 100vh;
  width: 120px;
  z-index: 10;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  background: transparent;
  pointer-events: none;
}


.main-content {
  flex: 1;
  margin-left: 90px;
  display: flex;
  align-items: center;
  height: 100vh;
}

.aboutme-container {
  border: 5px solid #111;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
